@keyframes showHint {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0.1;
  }
}

.settings {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  color: #fff;
  padding: 15px;
}

.settings__close {
  background: transparent;
  width: 64px;
  height: 64px;
  border: none;
  outline: none;
  color: #fff;
  font-size: 32px;
  float: right;
}

.settings__hint {
  display: inline-block;
  color: #91ff66;
  padding-top: 20px;
  opacity: 0;
  width: calc(100% - 64px);
}

.settings__hint--show {
  animation: showHint 3s linear;
}

.settings__form {
  margin-top: 64px;
}

.settings__lebel {
  display: block;
  padding-bottom: 30px;
}

.settings__select {
  width: 100%;
  height: 30px;
  margin-top: 10px;
}
