.game {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.game__reload {
  border: none;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  font-size: 32px;
  position: absolute;
  outline: none;
  background: #fff;
  color: #000;
}

.game__card {
  width: 90%;
  height: 0;
  padding-bottom: 140%;
  position: absolute;
}

.game__card-counter {
  position: absolute;
  right: 15px;
  bottom: 15px;
  color: #fff;
  font-size: 32px;
}
